.Popups{
    position: fixed;
    z-index: 1;
    top:0;
    left: 0;
    color: hotpink;
    margin:50% 40%;
    background-color: white;
    padding: 10px;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: all 0.4s ease-out;
   }

.open{
    transform: translateX(0);
}
.close{
    transform: translateX(-100%);
}