.SignUp {
    text-align: center;
    margin-top: 50px;
}

.succM {
    color: blue;
}

.already {
    background-color: white;
    color: blue;
}

button {
    outline: none;
    border: none;
    background-color: turquoise;
    cursor: pointer;
    padding: 4px 16px;
    border-radius: 20px;
    font-size: large;
}

input {
    padding: 8px 14px;
    font-size: large;
    margin: 10px 4px;
    outline: none;
    border: none;
    background-color: #EBECF0;
    border-radius: 20px;
}

.forml {
    display: flex;
    flex-direction: row;
}

@media(max-width:600px) {
    .forml {
        flex-direction: column;
    }
    button {
        font-size: large;
    }
    input {}
}