@media(max-width:600px) {
    .RoomBooking {
        margin-top: 10%;
        display: flex;
        flex-direction: column;
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
        padding: 10px;
        text-align: center;
        transition: all 0.3s ease-out;
        transform: translateX(0);
    }
    .headerRe img {
        max-width: 200px;
        max-height: 200px;
    }
}

.headerRe img {
    max-width: 200px;
}

.RoomBooking {
    margin-top: 5%;
    display: flex;
    flex-direction: column;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    padding: 10px;
    text-align: center;
    margin: auto;
    transition: all 0.3s ease-out;
    transform: translateX(0);
    max-width: 500px;
}

.input {
    padding: 16px 8px;
    border: none;
    font-size: large;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    margin: 10px;
}

.Link {
    text-decoration: none;
    color: brown;
    font-size: 20px;
}

.buttonContainer {
    display: flex;
    justify-content: space-around;
}

.button {
    border: 2px solid #2196F3;
    ;
    padding: 7px 14px;
    background-color: white;
    color: #2196F3;
    ;
    margin: 20px;
    text-decoration: none;
}

.button:hover {
    background-color: #2196F3;
    color: white;
}