.SearchRoom {
    text-align: center;
}

.input {
    margin: 20px;
    padding: 10px;
    font-size: 20px;
    border: none;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.3);
    font-size: large;
}

@media(max-width:600px) {
    .SearchRoom {
        display: flex;
        flex-direction: column;
        text-align: center;
    }
    .input {
        padding: 10px;
        font-size: 20px;
        border: none;
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.3);
    }
}

.Link {
    font-size: 25px;
    color: brown;
}