.AddRooms {
    display: flex;
    margin: auto;
    flex-direction: column;
    max-width: 500px;
}

.Link {
    text-decoration: none;
    text-align: center;
    color: brown;
    font-size: 20px;
}

.input {
    padding: 16px 8px;
    border: none;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    margin: 10px;
    font-size: large;
}

input[type='file'] {
    padding: 16px 8px;
    height: 20px;
    width: 480;
    background-color: brown;
}

.button {
    padding: 10px;
    align-self: center;
    border: none;
    background-color: #ccc;
}