.Body {
    display: flex;
    background-color: lightblue;
    flex-direction: row;
    justify-content: space-evenly;
}

@media(max-width:600px) {
    .Body {
        display: flex;
        flex-direction: row;
        max-width: 100%;
    }
}

.Body a {
    text-decoration: none;
    text-decoration: center;
    color: rgb(147, 240, 128);
    background-color: rgb(105, 165, 221);
    padding: 20px;
    margin: 0;
}

.Body a:hover,
.Body a:active,
.Body a.active {}