.Rooms {
    display: flex;
    flex-direction: column;
    margin-top: 10px;
    margin-bottom: 10px;
}

.Link {
    margin-top: 10px;
    padding: 8px 4px;
    border: none;
    outline: none;
    background-color: lightcyan;
    text-decoration: none;
    color: green;
    margin: auto;
}

.Link:hover {
    background-color: lightgreen;
}

img {
    max-width: 500px;
    margin: auto;
}

@media(max-width: 600px) {
    img {
        height: 340px;
        width: auto;
        margin: auto;
    }
}

